import React from 'react';
import moment from 'moment';

import TranslationsPage from './translation/TranslationsPage';
import ChaptersPage from './chapter/ChaptersPage';
import PassagePage from './passage/PassagePage';
import SettingsPage from './settings/SettingsPage';
import NotFoundPage from './notFound/NotFoundPage';

class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      currentPage: this.props.pages.PASSAGE,
      previousPage: this.props.pages.PASSAGE,
      translation: this.findTranslation(this.props.settings.preferredTranslation),
      chapterNumber: this.props.day,

      // Settings
      preferredTranslation: this.props.settings.preferredTranslation,
      textSize: this.props.settings.textSize,
      colorMode: this.props.settings.colorMode
    };
  }

  breakLines = (string, keyPrefix) => {
    const stringLines = [];
    const stringSplit = string.split(/\r\n/g);
    stringSplit.forEach((line, index) => {
      stringLines.push(
        <p key={keyPrefix + index}>{line}</p>
      );
    });

    return stringLines;
  }

  findTranslation = (abbr) => {
    return this.props.translations.filter(
        translation => translation.abbr === abbr)[0];
  }

  updateSettings = (settings) => {
    this.setState({
      translation: this.findTranslation(settings.preferredTranslation),
      preferredTranslation: settings.preferredTranslation,
      textSize: settings.textSize,
      colorMode: settings.colorMode
    });
  }

  gotoPage = (page) => {
    this.setState({
      previousPage: this.state.currentPage,
      currentPage: page
    });
  }

  gotoPreviousPage = () => {
    this.gotoPage(this.state.previousPage);
  }

  gotoChaptersPage = (abbr) => {
    this.setState({
      previousPage: this.state.currentPage,
      currentPage: this.props.pages.CHAPTERS,
      translation: this.findTranslation(abbr)
    });
  }

  gotoPassagePage = (chapter) => {
    this.setState({
      previousPage: this.state.currentPage,
      currentPage: this.props.pages.PASSAGE,
      chapterNumber: chapter
    });
  }

  gotoTodaysProverb = () => {
    this.setState({
      previousPage: this.state.currentPage,
      currentPage: this.props.pages.PASSAGE,
      chapterNumber: this.props.day
    });
  }

  renderPage() {
    switch(this.state.currentPage) {
      case this.props.pages.TRANSLATIONS:
        return <TranslationsPage
          translations={this.props.translations}

          // Functions
          gotoChaptersPage={this.gotoChaptersPage} />;

      case this.props.pages.CHAPTERS:
        return <ChaptersPage
          pages={this.props.pages}
          translation={this.state.translation}

          // Functions
          gotoPage={this.gotoPage}
          gotoPassagePage={this.gotoPassagePage}
          breakLines={this.breakLines} />;

      case this.props.pages.PASSAGE:
        return <PassagePage
          pages={this.props.pages}
          translation={this.state.translation}
          chapter={this.state.chapterNumber}
          textSize={this.state.textSize}

          // Functions
          gotoPage={this.gotoPage}
          breakLines={this.breakLines} />;

      case this.props.pages.SETTINGS:
        return <SettingsPage
          pages={this.props.pages}
          translations={this.props.translations}
          sizes={this.props.sizes}
          colorModes={this.props.colorModes}
          defaultSettings={this.props.defaultSettings}
          preferredTranslation={this.state.preferredTranslation}
          textSize={this.state.textSize}
          colorMode={this.state.colorMode}

          // Functions
          gotoPage={this.gotoPage}
          gotoPreviousPage={this.gotoPreviousPage}
          updateSettings={this.updateSettings} />;

      default:
        return <NotFoundPage />;
    }
  }

  render() {
    return (
      <main className={this.state.colorMode}>
        <header>
          <nav>
            <ul>
              <li>
                <button className="link" type="button" onClick={() => this.gotoTodaysProverb()}>
                  <i className="fal fa-clock"></i> Today's Proverb
                </button>
              </li>
              <li>
                <button className="link" type="button" onClick={() => this.gotoPage(this.props.pages.TRANSLATIONS)}>
                  <i className="fal fa-font"></i> Translations
                </button>
              </li>
            </ul>
          </nav>

          <button className="link" onClick={() => {this.gotoPage(this.state.previousPage)}}>
            <i className="fal fa-chevron-left"></i> Back
          </button>

          <div className="p"><i className="fal fa-calendar"></i>&nbsp;&nbsp;&nbsp;Date: {this.props.todaysDate}</div>
        </header>

        <section className="page">
          {this.renderPage()}
        </section>

        <footer>
          <p className="text-center text-muted"><small>{this.props.title} &copy; Davina Leong, {moment().format('Y')}</small></p>
        </footer>

        <button className="settings" type="button" onClick={() => this.gotoPage(this.props.pages.SETTINGS)}>
          <i className="fas fa-cog"></i></button>
      </main>
    );
  }
}

export default App;
