import React from 'react';
import ChapterCard from './ChapterCard';

function ChaptersPage(props) {
    const cards = [];
    props.translation.chapters.forEach((chapter, index) => {
        cards.push(
            <ChapterCard
                key={'c'+index}
                number={index+1}
                gotoPassagePage={props.gotoPassagePage} />
        );
    });

    return (
        <div>
            <h1 className="h3">{props.translation.name} ({props.translation.abbr})</h1>
            
            <section className="copyright">
                {props.breakLines(props.translation.copyright.text, 'l')}
            </section>

            <section className="cards">
                {cards}
            </section>
        </div>
    );
}

export default ChaptersPage;