import React from 'react';

function TranslationCard(props) {
    return (
        <div className="card">
            <h3 className="h4" onClick={() => props.gotoChaptersPage(props.translation.abbr)}>
                <button className="link" type="button">{props.translation.abbr}</button>
            </h3>
            <p>{props.translation.name}</p>
        </div>
    );
}

export default TranslationCard;