import React from 'react';

class SettingsPage extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            preferredTranslation: this.props.preferredTranslation,
            textSize: this.props.textSize,
            colorMode: this.props.colorMode
        };
    }

    updatePreferredTranslation = (event) => {
        this.setState({
            preferredTranslation: event.target.value
        });
    }

    updateTextSize = (event) => {
        this.setState({
            textSize: event.target.value
        });
    }

    updateColorMode = (event) => {
        this.setState({
            colorMode: event.target.value
        });
    }

    updateSettings = (settings) => {
        this.setState({
            preferredTranslation: settings.preferredTranslation,
            textSize: settings.textSize,
            colorMode: settings.colorMode
        });
    }

    saveSettings = () => {
        this.props.updateSettings({
            preferredTranslation: this.state.preferredTranslation,
            textSize: this.state.textSize,
            colorMode: this.state.colorMode
        });
    }

    saveDefaultSettings = () => {
        this.props.updateSettings({
            preferredTranslation: this.props.defaultSettings.preferredTranslation,
            textSize: this.props.defaultSettings.textSize,
            colorMode: this.props.defaultSettings.colorMode
        });
    }

    render() {
        const translations = [];
        this.props.translations.forEach((translation, index) => {
            translations.push(
                <option key={'ft' + index} value={translation.abbr}>{translation.name} ({translation.abbr})</option>
            );
        });

        const sizes = [];
        this.props.sizes.forEach((size, index) => {
            sizes.push(
                <option key={'fs' + index} value={size.value}>{size.name}</option>
            );
        });

        const colorModes = [];
        this.props.colorModes.forEach((colorMode, index) => {
            colorModes.push(
                <option key={'fcm' + index} value={colorMode.value}>{colorMode.name}</option>
            );
        });

        return (
            <div>
                <h1 className="h3">Settings</h1>

                <form className="card">
                    <table>
                        <tbody>
                        <tr>
                            <td><label htmlFor="translation">Preferred Translation</label></td>
                            <td>
                                <select name="translation" value={this.state.preferredTranslation}
                                    onChange={this.updatePreferredTranslation.bind(this)}>
                                    <option value="">-- Translations --</option>
                                    {translations}
                                </select>
                            </td>
                        </tr>
                        <tr>
                            <td><label htmlFor="size">Text Size</label></td>
                            <td>
                                <select name="size" value={this.state.textSize}
                                    onChange={this.updateTextSize.bind(this)}>
                                    <option value="">-- Sizes --</option>
                                    {sizes}
                                </select>
                            </td>
                        </tr>
                        <tr>
                            <td><label htmlFor="color">Color Modes</label></td>
                            <td>
                                <select name="color" value={this.state.colorMode}
                                    onChange={this.updateColorMode.bind(this)}>
                                    {colorModes}
                                </select>
                            </td>
                        </tr>
                        </tbody>
                        <tfoot>
                        <tr>
                            <td className="text-red" colSpan="2"><small>Click "Save" for changes to take place.</small></td>
                        </tr>
                        <tr>                                
                            <td colSpan="2">
                                <button className="mr" type="button"
                                    onClick={this.saveSettings.bind(this)}><i className="fal fa-save"></i> Save</button>
                                <button className="gray" type="button"
                                    onClick={this.saveDefaultSettings.bind(this)}><i className="fal fa-undo"></i> Default</button>
                            </td>
                        </tr>
                        </tfoot>
                    </table>
                </form>
            </div>
        );
    }
}

export default SettingsPage;