import React from 'react';

function PassagePage(props) {
    let passage = <p>&nbsp;</p>;
    if (props.translation.chapters[props.chapter - 1]) {
        passage = props.breakLines(props.translation.chapters[props.chapter - 1], 'p');
    }

    return (
        <div>
            <h1 className="h3">{props.translation.name} ({props.translation.abbr})</h1>

            <section className="copyright">
                {props.breakLines(props.translation.copyright.text, 'l')}
            </section>

            <h3 className="h5">Chapter {props.chapter}</h3>
            <p>
                <button className="link" type="button"
                    onClick={() => {props.gotoPage(props.pages.CHAPTERS)}}><i className="fal fa-hashtag"></i> View other chapters</button>
            </p>

            <div className={'passage size-' + props.textSize}>
                {passage}
            </div>
        </div>
    );
}

export default PassagePage;